* {
	user-select: none;
	outline: none;
	-webkit-tap-highlight-color: transparent;
	text-decoration: none;
	touch-action: none;
}

body {
	background-color: #000;
	margin: 0px;
	width: 100vw;
	height: 100vh;
	overflow: hidden;
}

#ani_dom {
	display: none !important;
}

/* ======================================================================== */

canvas {
	position: fixed;
	top: 0px;
	left: 0px;
}

.animate-view { z-index: 1; }
.diyazen-view { z-index: 3; }

@media (orientation: portrait) {
	.diyazen-view {
		top: 30px;
		left: 0;
		z-index: 2;
	}
}

/* ======================================================================== */

.spec_card {
	position: fixed;
	z-index: 4;
	width: 190px;
	height:60px;
	opacity: 0;
	transition: opacity 0.5s;
}

.spec_card_name {
	font-weight: bold;
	font-size: 17px;
	color: rgba(255,255,255,0.5);
}

.spec_card_info {
	font-size: 13px;
	color: rgba(255,255,255,0.3);
	margin-top: 5px;
}

@media (orientation: portrait) {
	.spec_card { width: 160px; }
	.spec_card_name { font-size: 13px; }
	.spec_card_info {
		font-size: 9px;
		color: rgba(255,255,255,0.5);
		margin-top: 2px;
	}
}

.diyazen_logo {
	z-index: 6;
	position: fixed;
	top: 20px;
	left: 20px;
	width: 160px;
	height: 40px;
	background-image: url(./bin/diyazen.png);
	background-repeat: no-repeat;
	background-size: contain;
	background-position: left;
}

@media (orientation: portrait) {
	.diyazen_logo {
		top: 10px;
		left: 10px;
		width: 160px;
		height: 50px;
	}
}